body {
  margin: 0;
  color: #000;
  font-family: Acme;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 3px;
}

* {
  margin: 0;
}

input,
button {
  border: none;
  background: none;
}

button {
  cursor: pointer;
}

input {
  font-family: Acme;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 3px;
}

.main-title {
  font-family: Karantina;
  font-size: 40px;
  font-weight: 700;
  letter-spacing: 6px;
  margin-bottom: 32px;
}

.body-container {
  width: 100vw;
  max-width: 400px;
  margin: 0 auto;
  padding: 40px 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

.loading-element {
  position: relative;
}

.search-form {
  display: flex;
  flex-direction: column;
  gap: 40px;
  margin-bottom: 50px;
}

.search-form__input-container {
  display: flex;
  flex-direction: column;
}

.search-form__label {
  font-size: 24px;
}

.search-form__input {
  border-radius: 15px;
  box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.25);
  height: 40px;
  width: 100%;
  box-sizing: border-box;
  padding: 5px 10px;
}

.search-form__button {
  position: relative;
  border-radius: 15px;
  border: 3px solid #bebebe;
  color: #000;
  font-family: Acme;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 3.6px;
  padding: 10px 0;
  transition: 0.5s;
  min-height: 56px;
}
.search-form__button:hover {
  border: 3px solid #828282;
  transition: 0.5s;
}
.search-form__button:disabled {
  opacity: 0.65;
  transition: 0.5s;
}

.loader {
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  position: absolute;
  width: 24px;
  height: 24px;
  border: 3px solid transparent;
  border-top: 3px solid #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}
.loader--center {
  left: 50%;
  right: auto;
  transform: translate(-50%, -50%);
  animation: centerSpin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: translateY(-50%) rotate(0deg);
  }
  100% {
    transform: translateY(-50%) rotate(360deg);
  }
}
@keyframes centerSpin {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
.response-block:not(:last-of-type) {
  margin-bottom: 50px;
}

.response-block__title {
  margin-bottom: 40px;
}

.response-block__fields-container {
  display: flex;
  flex-direction: column;
  gap: 25px;
  width: 100%;
}

.response-block-field {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-bottom: 5px;
  border-bottom: 2px solid #28cbff;
  box-sizing: border-box;
}

.response-block-field__title {
  font-size: 24px;
}

.response-block-field__text {
  min-width: 100px;
  min-height: 37px;
}
.response-block-field__text--url {
  max-width: 250px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.response-block-field__text--message {
  width: 100%;
}

.statistics .response-block-field {
  flex-direction: column;
  gap: 10px;
}
.statistics .loader {
  right: auto;
  left: 20px;
}